.logs {
  width: 100%;
  flex-basis: 100%;
  display: flex;
  flex-direction: column;
  border: 1px solid black;
  height: 250px;
  padding: 20px;
  gap: 15px 0;
  overflow-y: auto;
  &__item {
    font-size: 1.6rem;
  }
}

.crit {
  color: red;
  font-weight: bold;
}

.bold-colored {
  color: blue;
  font-weight: bold;
  text-transform: capitalize;
}